import React from 'react'

const Cards = ({ cards, player, handleCardClick }) => {
    return (
        <>
            <h1>{player} cards</h1>
            <ul>
                <li onClick={() => { handleCardClick(0, player) }}>{cards[0][1]}</li>
                <li onClick={() => { handleCardClick(1, player) }}>{cards[1][1]}</li>
            </ul>
        </>
    )
}

export default Cards;